import React, { useState, useEffect, useRef } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import axios from '../../utils/axios';
import MessageInput from './MessageInput';

const Chat = ({ selectedRoom }) => {
  const { user } = useAuth();
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [sendingMessage, setSendingMessage] = useState(false);
  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    if (selectedRoom?._id) {
      fetchMessages();
    }
  }, [selectedRoom?._id]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const fetchMessages = async () => {
    try {
      setLoading(true);
      setError('');
      
      // Use the correct API endpoint
      const response = await axios.get(`/api/messages`, {
        params: { roomId: selectedRoom._id }
      });
      
      // Handle case where no messages exist yet with empty array
      if (response.data && response.data.length > 0) {
        setMessages(response.data.reverse()); // Reverse to show newest at bottom
      } else {
        setMessages([]);
      }
    } catch (error) {
      console.error('Error fetching messages:', error);
      // Show a more user-friendly error message
      if (error.response?.status === 404) {
        setError('No messages found for this room yet');
        setMessages([]);
      } else {
        setError('Failed to fetch messages');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleSendMessage = async (content) => {
    if (!content || !content.trim()) {
      setError('Message cannot be empty');
      return;
    }
    
    try {
      setSendingMessage(true);
      setError('');
      
      // Use the send_message socket event instead of a direct API call
      const response = await axios.post(`/api/messages`, {
        roomId: selectedRoom._id,
        content: content.trim(),
        type: 'text'
      });
      
      setMessages(prev => [...prev, response.data]);
    } catch (error) {
      console.error('Error sending message:', error);
      const errorMessage = error.response?.data?.error || 'Failed to send message';
      setError(errorMessage);
    } finally {
      setSendingMessage(false);
    }
  };

  if (!selectedRoom) {
    return (
      <div className="flex-1 flex items-center justify-center text-gray-500">
        Select a room to start chatting
      </div>
    );
  }

  return (
    <div className="flex-1 flex flex-col h-full">
      <div className="p-4 border-b dark:border-gray-700">
        <h2 className="text-lg font-semibold">{selectedRoom.name}</h2>
        <p className="text-sm text-gray-500">{selectedRoom.description}</p>
      </div>

      {error && (
        <div className="p-3 bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-100">
          {error}
        </div>
      )}

      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {loading ? (
          <div className="flex items-center justify-center h-full">
            <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-primary-500"></div>
          </div>
        ) : messages.length === 0 ? (
          <div className="flex items-center justify-center h-full text-gray-500">
            No messages yet. Be the first to send a message!
          </div>
        ) : (
          messages.map((message) => (
            <div
              key={message._id}
              className={`flex ${
                message.sender._id === user._id ? 'justify-end' : 'justify-start'
              }`}
            >
              <div
                className={`max-w-[70%] rounded-lg p-3 ${
                  message.sender._id === user._id
                    ? 'bg-primary-500 text-white'
                    : 'bg-gray-100 dark:bg-gray-700'
                }`}
              >
                {message.sender._id !== user._id && (
                  <div className="text-xs text-gray-500 dark:text-gray-400 mb-1">
                    {message.sender.username}
                  </div>
                )}
                <div>{message.content}</div>
                <div className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                  {new Date(message.createdAt).toLocaleTimeString()}
                </div>
              </div>
            </div>
          ))
        )}
        <div ref={messagesEndRef} />
      </div>

      <MessageInput
        onSendMessage={handleSendMessage}
        disabled={loading || sendingMessage || !selectedRoom}
      />
    </div>
  );
};

export default Chat; 